/* Fonts */
/* Media Queries */
/* Colors */
:root {
  --bg-color: #ffffff;
  --primary-color: #007acc;
  --secondary-color: #b2b2b2;
  --primary-font-color: #121214;
  --secondary-font-color: #2a2a2a;
  --line-color: #27272a;
}

[data-theme=dark] {
  --bg-color: #121214;
  --primary-color: #007acc;
  --secondary-color: #27272a;
  --primary-font-color: #ffffff;
  --secondary-font-color: #b2b2b2;
  --line-color: #27272a;
}

/* Font Weights */
/** Media Queries **/
/* ----- Set the base font size on the HTML element for easier use of rem, em, and px. ----- */
html {
  font-size: 62.5%; /* 1rem = 10px (so, 1.6rem = 16px) */
  box-sizing: border-box;
  scroll-behavior: smooth;
  scroll-padding-top: 0rem;
  height: 100%;
}

/* ----- Inherit box-sizing in all elements and pseudo-elements ----- */
*,
*::before,
*::after {
  box-sizing: inherit;
}

/* ----- Set margins and padding in the body to avoid unwanted spaces ----- */
body {
  font-optical-sizing: auto;
  font-family: "Roboto", sans-serif;
  font-style: normal;
  font-size: 1.8rem;
  color: #000000;
  margin: 0;
  padding: 0;
  height: 100%;
  line-height: 1.8;
  background-color: var(--bg-color);
}

main {
  height: 100%;
  width: 100%;
  padding: 5.5rem;
  display: flex;
  gap: 5rem;
}
main .container {
  width: 50%;
  height: 100%;
}

/** ----- Global styles ----- **/
p {
  font-size: 2rem;
  color: #000000;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1,
h2,
h3 {
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  margin: 5rem;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

img {
  height: auto;
  display: block;
}

.reset-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.display-none {
  display: none;
}

.button {
  background: none;
  border: none;
}

/* layout styles */
/* Main task container */
.task {
  padding: 2.5rem;
  border: 0.1rem solid var(--line-color);
  border-radius: 2.5rem;
  /* Article container for task content */
}
.task article {
  display: flex;
  flex-direction: column;
  gap: 2.5rem;
  min-height: 100%;
}

/* Divider line below header */
.task__header-line {
  width: 90%;
  height: 0.1rem;
  background-color: var(--line-color);
}

/* Task header section */
.task__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: var(--primary-font-color);
  /* Container for title/subtitle */
}
.task__header div {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

/* Title styling */
.task__title,
.task__subtitle {
  margin: 0;
  line-height: 1.5;
}

.task__title {
  font-weight: 700;
}

/* Subtitle styling */
.task__subtitle {
  font-size: 2rem;
}

/* Checkmark container */
.task__check-container {
  width: 7.5rem;
  height: 7.5rem;
  padding: 1.5rem;
  border-radius: 1.5rem;
  background-color: var(--primary-color);
}

/* Main task body */
.task__body {
  flex: 1;
  /* Checklist items */
}
.task__body li {
  display: flex;
  align-items: center;
  gap: 1.5rem;
  /* Custom checkbox */
  /* Checkbox label */
}
.task__body li input[type=checkbox] {
  appearance: none;
  background-color: var(--bg-color);
  border: 2px solid var(--line-color);
  width: 20px;
  height: 20px;
  border-radius: 4px;
  cursor: pointer;
}
.task__body li input[type=checkbox]:checked {
  background-color: var(--primary-color);
}
.task__body li label {
  color: var(--secondary-font-color);
}

/* Task footer */
.task__footer {
  /* Form layout */
  /* Shared input styles */
  /* Text input field */
  /* Submit button */
}
.task__footer form {
  display: flex;
  justify-content: center;
  gap: 0.5rem;
}
.task__footer input {
  padding: 1rem;
  outline: none;
  border: none;
  border-radius: 0.5rem;
}
.task__footer .task__input {
  width: 70%;
  background-color: var(--secondary-color);
  color: var(--secondary-font-color);
}
.task__footer .task__submit {
  width: 25%;
  background-color: var(--primary-color);
  color: #ffffff;
  cursor: pointer;
}

.timer {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.timer__button:hover {
  cursor: pointer;
}

.timer__controls {
  position: absolute;
  top: 0;
  right: 0;
}

.timer__button--theme,
.timer__button--configuration {
  width: 5rem;
  height: 5rem;
}

.timer__time {
  font-size: 10rem;
  font-weight: 700;
  color: var(--primary-font-color);
  margin: 0;
  line-height: 1.5;
}

.timer__footer {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--secondary-color);
  padding: 2rem;
  border-radius: 5rem;
}